@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html body {
  background-color: #1f2632;
  font-family: "Manrope", Arial, sans-serif;
  color: #cee3e9;
}

.flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.main-container {
  background-color: #323a49;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 50px 10px #1f2632;
  border-radius: 15px;
  padding: 40px;
  width: 500px;
  margin: 13% auto;
  position: relative;
}

#adviceId {
  letter-spacing: 6px;
  color: #52ffa8;
  font-size: 0.7em;
  margin-bottom: 20px;
}

#message {
  font-size: 25px;
  margin-bottom: 30px;
}

.divider-one, .divider-two {
  margin-bottom: 25px;
}

.divider-two {
  display: none;
}

.attribution-container {
  font-size: 0.6em;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  color: #52ffa8;
}
a:active {
  color: #43baff;
}

#dice-btn {
  border-radius: 50%;
  height: 60px;
  width: 60px;
  position: relative;
  border: 0;
  background-color: #52ffa8;
  position: absolute;
  bottom: -30px;
  transition: all 0.3s;
}
#dice-btn .dice {
  position: absolute;
  bottom: 18px;
  left: 18px;
}
#dice-btn:hover {
  box-shadow: 0 0 30px 3px #52ffa8;
  cursor: pointer;
  transform: scale(1.1, 1.1);
}
#dice-btn:active {
  transform: rotate(180deg);
}

@media only screen and (max-width: 520px) {
  .main-container {
    width: 350px;
    margin-top: 20%;
    margin-bottom: 20%;
    padding: 30px;
  }

  #message {
    font-size: 23px;
  }

  .divider-one {
    display: none;
  }

  .divider-two {
    display: block;
  }
}